@include tablet {
    .seccion-inferior {
        display: grid;
        grid-template-columns: 2fr 1fr;
        column-gap: 2rem;
    }
}

.entrada-blog {
    margin-bottom: $separacion / 2;

    &:last-of-type {
        margin-bottom: 0;
    }

    @include tablet {
        display: grid;
        grid-template-columns: 1fr 2fr;
        column-gap: 2rem;
    }
}

.texto-entrada {
    a {
        color: $negro;
        display: block;
        margin-top: 2rem;

        @include tablet {
            margin-top: 0;
        }
    }

    h4 {
        margin: 0;
        line-height: 1.4;

        &::after {
            content: '';
            display: block;
            width: 15rem;
            height: 0.5rem;
            background-color: $verde;
            margin-top: 1rem;
        }
    }

    p {
        text-align: justify;
    }
}

.informacion-meta {
    span {
        color: $amarillo;
    }

    p {
        margin-top: 2rem;
    }
}